import * as React from "../_snowpack/pkg/react.js";
export class PlayerCommander extends React.Component {
  componentDidUpdate() {
    const { player, commands } = this.props;
    commands.forEach((cmd) => {
      switch (cmd.type) {
        case "play":
          player.playVideo();
          break;
        case "cue":
          player.cueVideoById(cmd.videoId, cmd.timestamp);
          break;
        case "seek":
          player.seekTo(cmd.timestamp);
          break;
        default:
          console.error("unexpected video command type", cmd.type, cmd);
      }
    });
  }
  render() {
    return null;
  }
}
