import { createChannel } from "../Channel.js";
import { albums } from "./music.js";
import {
  channel1Episodes,
  channel1Videos,
  channel2Videos,
  channel4Videos,
  debuggingVideos
} from "./shows.js";
import { christmasAlbums, christmasSongs } from "./christmas.js";
export const channels = [
  createChannel("Channel 1", [
    ...channel1Episodes,
    ...channel1Videos.map(singleVideoEpisode)
  ]),
  createChannel("Channel 2", channel2Videos.map(singleVideoEpisode)),
  createChannel("Channel 3", albums),
  createChannel("Channel 4", channel4Videos.map(singleVideoEpisode)),
  createChannel("", [
    ...christmasAlbums,
    ...christmasSongs.map(singleVideoEpisode)
  ])
];
function singleVideoEpisode(v) {
  return { videos: [v] };
}
