import { expect, is, test } from "../../_snowpack/pkg/@benchristel/taste.js";
export const pipe = (...fns) => {
  return fns.reduce(rcompose);
};
test("pipe", {
  "given one function"() {
    const inc = (x) => x + 1;
    expect(pipe(inc)(1), is, 2);
  },
  "given two functions"() {
    const inc = (x) => x + 1;
    expect(pipe(inc, inc)(1), is, 3);
  }
});
export function rcompose(f, g) {
  return (...args) => g(f(...args));
}
export function map(f) {
  return (array) => array.map(f);
}
